@import url(https://fonts.googleapis.com/css2?family=Concert+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap);

.App {
  text-align: center;
}

#name {
  text-align: left;
  font-family: 'Concert One', cursive;
  font-size: 40px;
}

.header {
  padding: 10px;
  text-align: center;
  background: rgb(223, 59, 18);
  color: white;
  font-size: 10px;
}

.navigation {
  padding: 5px;
  font-family: 'Roboto', sans-serif;
}

.item {
  margin-right: 40px;
  font-family: sans-serif;
  font-size: medium;
  color: white;
  text-decoration: none;
}

.item:hover {
  text-decoration: underline;
}

.title {
  font-family: 'Concert One', cursive;
  font-size: 30px;
  text-align: center;
}

.text {
  font-family: 'Roboto', sans-serif;
}

#welcome {
  font-size: larger;
}

.project-title {
  font-family: 'Roboto', sans-serif;
  font-size: large;
  text-decoration: none;
  color: black;
  position: absolute;
  bottom: 7%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.project-category {
  font-family: 'Roboto', 'sans-serif';
  font-size: x-small;
  text-decoration: none;
  color: black;
  position: absolute;
  bottom: 0%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

@keyframes fadein {
  from {opacity:0;}
  to {opacity:1;}
}

@-webkit-keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

.container {
  border: 2px solid black;
  border-radius: 10px;
  position: relative;
  height: 220px;
  width: 300px;
  padding: 5px;
  -webkit-animation: fadein 7s;
  animation: fadein 7s;
}

.flexbox {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 5px;
}

.flexbox > .flexchild {
  margin: 5px;
}

li {
  font-family: 'Roboto', sans-serif;
}





